<template>
  <div id="chat_123876">
    <transition name="bounce">
      <div
        :style="[
          showPop
            ? 'border: 1px solid rgb(242, 242, 242); box-shadow: 6px 2px 15px 5px rgba(0, 0, 0, 0.1);'
            : '',
        ]"
      >
        <div v-if="isLoading" class="loading-div"></div>
        <chatmaintest
          :checkFirst="checkFirst"
          @updateState="updateState"
          :addConversation="addConversation"
          :updateConversation="updateConversation"
          :removeConversation="removeConversation"
          :getConversationBy="getConversationBy"
          :showChat="showChat"
          :showPop="showPop"
          :stateval="stateval"
          :user="user"
          :unreadMsg="unreadMsg"
          :botName="botName"
          @createNewConvo="createNewConvo"
          @updateUnreadMsg="updateUnreadMsg"
          v-if="showPop"
        />
      </div>
    </transition>

    <div class="chat-icon" @click="toggleChat()">
      <transition name="bounce">
        <svg
          v-if="!showPop"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 512 512"
        >
          <path
            d="M64 0C28.7 0 0 28.7 0 64V352c0 35.3 28.7 64 64 64h96v80c0 6.1 3.4 11.6 8.8 14.3s11.9 2.1 16.8-1.5L309.3 416H448c35.3 0 64-28.7 64-64V64c0-35.3-28.7-64-64-64H64z"
          />
        </svg>

        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          v-else
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M18.601 8.39897C18.269 8.06702 17.7309 8.06702 17.3989 8.39897L12 13.7979L6.60099 8.39897C6.26904 8.06702 5.73086 8.06702 5.39891 8.39897C5.06696 8.73091 5.06696 9.2691 5.39891 9.60105L11.3989 15.601C11.7309 15.933 12.269 15.933 12.601 15.601L18.601 9.60105C18.9329 9.2691 18.9329 8.73091 18.601 8.39897Z"
            fill="white"
          ></path>
        </svg>
      </transition>
    </div>
  </div>
</template>

<script>
import { createConversationId, getLastChat } from "@/utils/firbase_utils";
import chatmaintest from "./views/test/chatmaintest.vue";

export default {
  data() {
    return {
      showChat: false,
      showPop: false,
      user: "",
      ip: "",
      stateval: {},
      unreadMsg: 0,
      botName: "Alex",
      isLoading: false,
    };
  },
  components: { chatmaintest },

  methods: {
    updateUnreadMsg(value) {
      this.unreadMsg = this.unreadMsg + value;
      //   console.log(value, "updating unread messge", this.unreadMsg);
    },
    toggleChat() {
      this.showPop = !this.showPop;
      const newState = {
        ...this.stateval,
        state: {
          ...this.stateval.state,
          toggle: this.showPop,
        },
      };
      this.updateState(newState);
    },

    async checkFirst() {
      let statecheck = localStorage.getItem("appState");
      if (statecheck === null) {
        let data = {
          user: {
            role: "lead",
            locale: "en",
            hasConversations: true,
          },
          conversations: {
            byId: {},
          },
          state: {
            page: {
              name: "home",
              id: null,
            },
            toggle: false,
          },
        };
        localStorage.setItem("appState", JSON.stringify(data));
      }
    },
    async updateState(newState) {
      this.stateval = newState;
      localStorage.setItem("appState", JSON.stringify(newState));
    },
    addConversation(conversationId, conversationData) {
      const newState = {
        ...this.stateval,
        conversations: {
          ...this.stateval.conversations,
          byId: {
            ...this.stateval.conversations.byId,
            [conversationId]: conversationData,
          },
        },
      };
      this.updateState(newState);
    },
    updateConversation(conversationId, conversationData) {
      const newState = {
        ...this.stateval,
        conversations: {
          ...this.stateval.conversations,
          byId: {
            ...this.stateval.conversations.byId,
            [conversationId]: {
              ...this.stateval.conversations.byId[conversationId],
              ...conversationData,
            },
          },
        },
      };
      this.updateState(newState);
    },
    getConversationBy(conversationId) {
      const data = JSON.parse(localStorage.getItem("appState"));
      const conversations = data.conversations.byId;
      if (conversations.hasOwnProperty(conversationId)) {
        return conversations[conversationId];
      } else {
        return null;
      }
    },
    removeConversation(conversationId) {
      const {
        [conversationId]: removedConversation,
        ...remainingConversations
      } = this.stateval.conversations.byId;
      const newState = {
        ...this.stateval,
        conversations: {
          ...this.stateval.conversations,
          byId: remainingConversations,
        },
      };
      this.updateState(newState);
    },

    async get_creat_user() {
      // Check if we have a user ID cookie
      // this.setCookie("userId", "10511221794", 7)
      // const userIdCookie = await this.getCookie("userId");

      const userIdCookie = localStorage.getItem("userId");

      // console.log(userIdCookie, "okay the this");

      if (userIdCookie) {
        // If we have a user ID cookie, use that
        this.user = userIdCookie;
      } else {
        // Otherwise, fetch the user's IP address and create a new user ID
        await fetch("https://api.ipify.org?format=json")
          .then((x) => x.json())
          .then(({ ip }) => {
            this.user = ip.split(".").join("");
            this.ip = ip;
          });

        // Set the new user ID as a cookie
        // Cookies.set("userId", this.user, { expires: 365 });
        localStorage.setItem("userId", this.user);
      }

      // console.log(this.ip, this.user, "hellooooooooo");
    },

    async saveConversationId(conversationId) {
      // Check if conversation ID has expired

      const timestamp = Date.now();

      const conversationData = {
        fetchState: "partial",
        read: true,
        expire: timestamp,
        ended: false,
      };
      this.addConversation(conversationId, conversationData);

      // localStorage.setItem("conversationId", conversationData);
    },

    async createNewConvo() {
      this.isLoading = true;
      const savedState = JSON.parse(localStorage.getItem("appState"));

      const conversationsById = savedState.conversations.byId;

      const lastIdKey = Object.keys(conversationsById).pop();

      const lastConversation = Object.values(conversationsById).pop();
      const lastConversationEnded = lastConversation
        ? lastConversation.ended
        : true;
      //   console.log(
      //     "Last conversation ended:",
      //     lastConversationEnded,
      //     lastConversation,
      //     lastIdKey
      //   );

      if (lastConversationEnded) {
        this.isLoading = true;
        let chatId = await createConversationId(this.user);
        // console.log(chatId);
        await this.saveConversationId(chatId);
        this.isLoading = false;
        this.$router.push({
          name: "conversation",
          params: { id: chatId },
        });
      } else {
        this.$router.push({
          name: "conversation",
          params: { id: lastIdKey },
        });
        this.isLoading = false;
      }
    },
  },

  async mounted() {
    this.checkFirst();

    const savedState =
      JSON.parse(localStorage.getItem("appState")) || this.stateval;
    await this.updateState(savedState);

    this.removeConversation("undefined");

    // this.addConversation("ei6I4cTFfnyA6KeiQXck", {
    //   fetchState: "partial",
    //   read: true,
    //   expire: 1679263135957,
    // });
  },

  async created() {
    await this.checkFirst();
    await this.get_creat_user();
    const appState = JSON.parse(localStorage.getItem("appState")) ;
    // console.log("Created...", appState.router.location.name);
    if (appState && appState.hasOwnProperty('state')) {
    
      this.showPop = appState.state.toggle;
      
    }else{
        const newState = {
        ...appState,
        state: {
          toggle: false,
          page: {
            name: "home",
            id: null,
          },
        },
      };
      this.updateState(newState);
      }
  },
};
</script>

<style scope>
.badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: red;
  color: white;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chat-transition {
  transition: all 0.3s ease-out;
}

#chat_123876 .chat-icon {
  position: fixed;
  bottom: 20px;
  right: 20px;
  cursor: pointer;
  background: #1263ff;
  padding: 15px;
  border-radius: 50%;
  width: 55px;
  height: 52px;
  z-index: 999999999;
}

#chat_123876 .chat-icon svg {
  fill: #fff;
}

.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
</style>
