<template>
  <div :aria-valuetext="message.saved" v-if="!message.saved">
    <div
      v-for="(input, index) in message.inputs"
      :key="index"
      class="input-div"
    >
      <div class="input-placeholder">
        {{ capitalizeName(input.placeholder) }} {{ message.saved }}
      </div>
      <div class="input-container">
        <form @submit.prevent="handleInputSubmit(input, index)">
          <input
            required
            :type="input.type"
            autocomplete="off"
            :aria-label="input.placeholder"
            :ref="`inputField_${input.placeholder}_${index}`"
          />
          <button v-if="input" type="submit" aria-label="Submit" tabindex="0">
            <span aria-hidden="true" class="">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 16 16"
                fill="none"
              >
                <path
                  d="M5.42773 4.70898C5.46387 4.85254 5.53809 4.98828 5.65039 5.10059L8.54932 8L5.64893 10.9004C5.31689 11.2324 5.31689 11.7705 5.64893 12.1025C5.98096 12.4336 6.51904 12.4336 6.85107 12.1025L10.3516 8.60059C10.5591 8.39355 10.6367 8.10449 10.585 7.83691C10.5537 7.67578 10.4761 7.52246 10.3516 7.39844L6.85254 3.89941C6.52051 3.56738 5.98242 3.56738 5.65039 3.89941C5.43066 4.11816 5.35645 4.42871 5.42773 4.70898Z"
                  fill="#ffffff"
                ></path>
              </svg>
            </span>
          </button>
        </form>
      </div>
    </div>
  </div>
  <div :aria-valuetext="message.saved" v-else>
    <div :key="index" class="input-div">
      <div class="input-placeholder">
        {{ capitalizeName(message.placeholder) }}
      </div>
      <div class="input-container">
        <input
          disabled
          required
          :type="message.type"
          autocomplete="off"
          :value="message.value"
          :aria-label="message.placeholder"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["message"],
  methods: {
    async handleInputSubmit(msg, index) {
      const inputValue = event.target.elements[0].value.trim();
      const value = `My ${msg.placeholder} is: ${inputValue}`;

      this.$emit("handleInputSubmit", {
        msg: msg,
        inputValue: inputValue,
        value: value,
      });
    },
    capitalizeName(name) {
      // console.log(name, "the name");
      return name.charAt(0).toUpperCase() + name.slice(1);
    },
  },
};
</script>

<style scoped>
.input-div {
  position: relative;
  padding: 26px 24px 24px;
  margin-left: 45px;
  text-align: left;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  color: rgb(0, 0, 0);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 15px 0px,
    rgba(0, 0, 0, 0.1) 0px 1px 2px 0px,
    rgba(0, 87, 255, 0.5) 0px 2px 0px 0px inset;
}

.input-div .input-placeholder {
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 20px;
}

.input-container {
  position: relative;
}

.input-container input {
  width: 100%;
  height: 40px;
  padding: 11px 40px 11px 16px;
  box-sizing: border-box;
  font-size: 14px;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 3px 0px inset;
  border: 1px solid rgb(225, 225, 225);
  color: rgb(0, 0, 0);
  border-radius: 4px;
  background: rgb(250, 250, 250);
  appearance: none;
}

.input-container button {
  cursor: pointer;
  z-index: 1;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  width: 40px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  background-color: rgb(0, 87, 255);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}
</style>
