import { StartFirebase, storage } from "./firebaseDb";
import {
  collection,
  getDocs,
  getDoc,
  doc,
  query,
  where,
  addDoc,
  serverTimestamp,
  orderBy,
  updateDoc,
  limit,
} from "firebase/firestore";
const db = StartFirebase();

const createConversationId = async (user) => {
  const dbref = collection(db, "conversation");
  let val = "";
  try {
    const docRef = await addDoc(dbref, { step: 0, user: user, agent: "" }).then(
      async (res) => {
        // console.log(res, "this is the resspin");
        // console.log("Document written with ID: ", res.id);
        val = res.id;
        const conversationDocRef = doc(db, "conversation", val);
      }
    );
  } catch (error) {
    return false;
  }
  return val;
};

