const setStepConversation = async (id, step) => {
  const collectRef = collection(db, "conversation");
  try {
    await updateDoc(doc(collectRef, id), { step: step });
  } catch (error) {
    console.log(error);
  }
};

const updateConversationAgent = async (id, agent) => {
  const collectRef = collection(db, "conversation");
  try {
    await updateDoc(doc(collectRef, id), { agent: agent });
  } catch (error) {
    console.log(error);
  }
};

const saveChats = async (message, id) => {
  console.log(message, id, "trying to save");

  const parentDocRef = doc(db, "conversation", id);
  const subcollectionRef = collection(parentDocRef, "chat");

  const { text, type, user, isUser } = message;

  const newMessage = {
    ...message,
    createdAt: serverTimestamp(),
  };

  // Add a new document to the sub-collection with the current timestamp
  await addDoc(subcollectionRef, newMessage).then(async (res) => {
    console.log("Document written with ID: ", res.id);

    // Update the conversation document to indicate that a new message has been added
    if (isUser) {
      await updateDoc(parentDocRef, {
        agentNewmessage: true,
        lastMsgTime: serverTimestamp(),
      });
      console.log("Conversation document updated with agentNewmessage: true");
    } else {
      await updateDoc(parentDocRef, {
        userNewmessage: true,
        lastMsgTime: serverTimestamp(),
      });
      console.log("Conversation document updated with userNewmessage: true");
    }
  });
};

const getConversation = async (id) => {
  const snap = await getDoc(doc(db, "conversation", id));

  if (snap.exists()) {
    // console.log(snap.data(), "steppsss");
    return snap.data();
  } else {
    console.log("No such document");
  }
};

const getLastChat = async (id) => {
  // Create a reference to the parent document that contains the sub-collection
  const parentDocRef = doc(db, "conversation", id);

  // Create a reference to the sub-collection and order by "createdAt" in descending order
  const subcollectionRef = collection(parentDocRef, "chat");
  const queryVal = query(
    subcollectionRef,
    orderBy("createdAt", "desc"),
    limit(1)
  );

  let parentData = await getDoc(doc(db, "conversation", id));
  if (parentData.exists()) {
    // console.log(snap.data(), "steppsss");
    parentData = parentData.data();
  }
  // Get the first document in the query snapshot
  const querySnapshot = await getDocs(queryVal);

  // console.log(querySnapshot.docs[0].data(), "fetch data");
  if (querySnapshot.docs.length > 0) {
    const lastChatData = querySnapshot.docs[0].data();
    return { lastChatData, parentData };
  } else {
    return { lastChatData: null, parentData };
  }
};

const findRefobj = async (table, obj, objval) => {
  // console.log(table, obj, objval);
  const refCollectionRef = collection(db, table);
  const q = query(refCollectionRef, where(obj, "==", objval));
  const querySnapshot = await getDocs(q);
  // console.log("got here again", querySnapshot);
  if (querySnapshot.size === 0) {
    // console.log("false");
    return false;
  }

  // console.log("got here again");

  // console.log("size", querySnapshot.docs[0].data());

  let valdata = querySnapshot.docs[0].data();
  let value = {
    wallet: valdata.wallet,
    ref_code: valdata.ref_code,
  };

  // console.log(value, "value");

  return valdata;
};

const updateConvo = async (id, data) => {
  const parentDocRef = doc(db, "conversation", id);
  await updateDoc(parentDocRef, data);
};

const getConvoChats = async (id) => {
  // Create a reference to the parent document that contains the sub-collection
  // console.log("convo ID", id);
  const parentDocRef = doc(db, "conversation", id);
  const value = [];

  // Create a reference to the sub-collection
  // const subcollectionRef = collection(parentDocRef, "chat");

  // Create a reference to the sub-collection and order by "createdAt" in descending order
  const subcollectionRef = collection(parentDocRef, "chat");

  const orderedSubcollection = query(
    subcollectionRef,
    orderBy("createdAt", "asc")
  );

  await updateDoc(parentDocRef, { userNewmessage: false });

  // Get the documents in the sub-collection ordered by "createdAt"
  const querySnapshot = await getDocs(orderedSubcollection);

  // console.log("docs", querySnapshot);

  // Iterate over the documents in the query snapshot
  querySnapshot.forEach((doc) => {
    // console.log(doc.id, " => ", doc.data());
    value.push(doc.data());
  });

  return value;
};

const readMessage = async (id) => {
  // Create a reference to the parent document that contains the sub-collection
  // console.log("convo ID", id);
  const parentDocRef = doc(db, "conversation", id);

  await updateDoc(parentDocRef, { userNewmessage: false });
};

export {
  setStepConversation,
  createConversationId,
  saveChats,
  getConvoChats,
  getStepConversation,
  getLastChat,
  updateConversationAgent,
  getConversationAgent,
  findRefobj,
  readMessage,
  getConversation,
  updateConvo,
};
