<template>
  <div v-if="loaded">
    <v-list-item
      :title="convo.text"
      :subtitle="convo.subtitle"
      :prepend-avatar="imageSrc"
      append-icon
    >
      <template #append>
        <v-badge color="green" v-if="convo.userNewmessage">
          <!-- <v-icon small>mdi-circle-medium</v-icon> -->
        </v-badge>
      </template>
      <template v-slot:subtitle="{}">
        <span class="text-primary">
          {{
            capitalizeName(
              convo.user === "bot"
                ? "Alex"
                : convo.user === user
                ? "you"
                : agent || "bot"
            )
          }}
        </span>
        &mdash; {{ timeAgo(convo.createdAt) }}
      </template>
    </v-list-item>
    <v-divider inset></v-divider>
  </div>
</template>

<script>
import { getLastChat } from "@/utils/firbase_utils";
import dayjs from "dayjs";
import relativeTime from "dayjs/plugin/relativeTime";

dayjs.extend(relativeTime);
export default {
  data: () => ({
    loaded: false,
  }),
  props: ["convo", "agent", "user"],
  components: {},

  methods: {
    capitalizeName(name) {
      return name.charAt(0).toUpperCase() + name.slice(1);
    },
    timeAgo(timestamp) {
      const now = dayjs();
      const datetime = dayjs(timestamp.toDate());
      let diff = datetime.diff(now, "second");
      let suffix = diff > 0 ? " ago" : " ago";
      diff = Math.abs(diff);

      if (diff < 60) {
        return `Just now`;
      } else if (diff < 3600) {
        let minutes = Math.floor(diff / 60);
        return `${minutes} ${minutes > 1 ? "minutes" : "minute"}${suffix}`;
      } else if (diff < 86400) {
        let hours = Math.floor(diff / 3600);
        return `${hours} ${hours > 1 ? "hours" : "hour"}${suffix}`;
      } else {
        let days = Math.floor(diff / 86400);
        return `${days} ${days > 1 ? "days" : "day"}${suffix}`;
      }
    },
  },

  async created() {
    console.log(this.convo, "the convovov");
    this.loaded = true;
  },
  computed: {
    displayAgent() {
      // let theUser = this.agent || "bot";
      // if (this.convo.user === "bot") {
      //   theUser = this.convo.user;
      // }
      return this.convo.user === "bot" ? "Alex" : this.agent || "bot";
    },

    imageSrc() {
      // console.log(this.displayAgent, "the agent ");
      const url = `https://ui-avatars.com/api/?name=${this.displayAgent}&size=40&background=%23ccc&color=fff&color=fff&rounded=true&font-size=0.5`;
      return url;
    },

    // ...other computed properties
  },
};
</script>

<style scoped>
.v-list-item {
  cursor: pointer !important;
}
</style>
