<template>
  <canvas ref="canvas"></canvas>
</template>

<script>
export default {
  props: {
    name: String,
    size: {
      type: Number,
      default: 40,
    },
  },
  mounted() {
    const canvas = this.$refs.canvas;
    const context = canvas.getContext("2d");
    const size = this.size; // Size of the canvas
    const fontSize = size / 2; // Font size of the letter
    const fontFamily = "Arial"; // Font family of the letter

    // Set the canvas size
    canvas.width = size;
    canvas.height = size;

    // Draw the letter
    context.fillStyle = "#ccc"; // Background color
    context.fillRect(0, 0, size, size);
    context.fillStyle = "#fff"; // Text color
    context.textAlign = "center";
    context.textBaseline = "middle";
    context.font = `${fontSize}px ${fontFamily}`;
    context.fillText(this.getNameInitial(), size / 2, size / 2);
  },
  methods: {
    getNameInitial() {
      return this.name ? this.name[0].toUpperCase() : "B";
    },
  },
  watch: {
    name: function (newValue, oldValue) {
      console.log(newValue, "there was a chnage");
      const canvas = this.$refs.canvas;
      const context = canvas.getContext("2d");
      const size = 40; // Size of the canvas
      const fontSize = size / 2; // Font size of the letter
      const fontFamily = "Arial"; // Font family of the letter

      // Set the canvas size
      canvas.width = size;
      canvas.height = size;

      // Draw the letter
      context.fillStyle = "#ccc"; // Background color
      context.fillRect(0, 0, size, size);
      context.fillStyle = "#fff"; // Text color
      context.textAlign = "center";
      context.textBaseline = "middle";
      context.font = `${fontSize}px ${fontFamily}`;
      context.fillText(this.getNameInitial(), size / 2, size / 2);
      // Re-mount the component
      // this.$nextTick(() => {
      //   this.$forceUpdate();
      // });
    },
  },
};
</script>
<style scoped>
canvas {
  border-radius: 50%;
}
</style>
