<template>
  <div>
    <div v-if="!showingConvo">
      <div id="chatview">
        <transition name="bounce">
          <div class="chat-container">
            <div class="chat-header">
              <h3>Chatly</h3>
              <!-- <i class="fa fa-times" @click="showPop = false"></i> -->
            </div>
            <div ref="chatbody" class="chat-body" id="chat-body">
              <div v-if="isLoading" class="loading-div"></div>
              <!-- Line loader for chat timeout -->
              <!-- <div class="line-loader" :style="{ width: loaderWidth + '%' }"></div> -->
              <div class="message-box">
                <v-list
                  v-if="activeConversations.length > 0"
                  item-props
                  lines="two"
                >
                  <h3>Current conversation</h3>
                  <SingleChat
                    :convo="convo"
                    v-for="convo in activeConversations"
                    :key="convo.text"
                    :agent="convo.agent"
                    :user="user"
                    @updateUnreadMsg="updateUnreadMsg"
                    class="chat-transition"
                    @click="show(convo.conversationId)"
                  />
                </v-list>
                <v-list
                  v-if="endedConversations.length > 0"
                  item-props
                  lines="two"
                >
                  <h3>Previous Closed Conversations</h3>
                  <SingleChat
                    :convo="convo"
                    v-for="convo in endedConversations"
                    :key="convo.text"
                    :agent="convo.agent"
                    :user="user"
                    @updateUnreadMsg="updateUnreadMsg"
                    class="chat-transition"
                    @click="show(convo.conversationId)"
                  />
                </v-list>
              </div>
            </div>

            <div class="chat-footer">
              <v-btn
                @click="createNewConvo"
                class="start"
                :aria-details="getCount"
                getCount
                v-if="!getCount && !isLoading"
              >
                Start Conversation
              </v-btn>
            </div>
          </div>
        </transition>
      </div>
    </div>
    <test
      :botName="botName"
      :user="user"
      :conversationId="conversationId"
      @goBack="goBack"
      @goBackTo="goBackTo"
      @createNewConvo="createNewConvo"
      v-else
    />
  </div>
</template>

<script>
import SingleChat from "../../components/test/singleChat.vue";
import { createConversationId, getLastChat } from "@/utils/firbase_utils";
import io from "socket.io-client";
import { Timestamp } from "firebase/firestore";
import test from "./testing.vue";
export default {
  data() {
    return {
      socketMainpage: null,
      chatActive: false,
      isLoading: false,
      conversations: [],
      showingConvo: false,
      conversationId: "",
      state: null,
    };
  },
  props: ["showPop", "addConversation", "user", "botName", "stateval"],
  components: {
    SingleChat,
    test,
  },
  methods: {
    show(convoId) {
      this.conversationId = "";
      this.conversationId = convoId;
      this.showingConvo = true;
      const data = JSON.parse(localStorage.getItem("appState"));
      this.state = data;
      const newState = {
        ...this.state,
        state: {
          ...this.state.state,
          page: {
            name: "conversation",
            id: convoId,
          },
        },
      };
      this.$emit("updateState", newState);
    },

    async goBack() {
      this.showingConvo = false;
      await this.init();
      this.$forceUpdate();
      const data = JSON.parse(localStorage.getItem("appState"));
      this.state = data;
      const newState = {
        ...this.state,
        state: {
          ...this.state.state,
          page: {
            name: "home",
            id: null,
          },
        },
      };
      this.$emit("updateState", newState);
    },

    async goBackTo() {
      this.showingConvo = false;
      await this.init();
      this.$forceUpdate();
    },

    updateUnreadMsg() {
      this.$emit("updateUnreadMsg");
    },

    async saveConversationId(conversationId) {
      // Check if conversation ID has expired

      const timestamp = Date.now();

      const conversationData = {
        fetchState: "partial",
        read: true,
        expire: timestamp,
        ended: false,
      };
      this.addConversation(conversationId, conversationData);

      // localStorage.setItem("conversationId", conversationData);
    },

    handleNewMessage(newMessage) {
      //   console.log(newMessage.createdAt, "time check");
      const conversationIndex = this.conversations.findIndex(
        (c) => c.conversationId === newMessage.conversationId
      );
      if (conversationIndex > -1) {
        // Update the conversation with the new message text
        const conversation = this.conversations[conversationIndex];
        conversation.text = newMessage.text;
        conversation.userNewmessage = true;
        conversation.createdAt = newMessage.createdAt;
        this.conversations.splice(conversationIndex, 1, conversation);

        // Move the conversation to the top of the list
      }
    },

    async getAllInfo(conversation) {
      const result = await getLastChat(conversation);

      let userNewmessage = (await result.parentData.userNewmessage) || false;
      let lastMsg = await result.lastChatData;
      if (result.lastChatData === null) {
        lastMsg = {
          text: "hello message",
          user: "Bot",
          createdAt: Timestamp.now(),
        };
      } else if (result.lastChatData.type === "voice") {
        lastMsg = {
          text: "voice Message",
          user: result.lastChatData.user,
          createdAt: result.lastChatData.createdAt,
        };
      } else if (result.lastChatData.type === "image") {
        lastMsg = {
          text: "Image",
          user: result.lastChatData.user,
          createdAt: result.lastChatData.createdAt,
        };
      }
      let ended;
      if (result.parentData.hasOwnProperty("ended")) {
        ended = result.parentData.ended;
      } else {
        ended = false;
      }

      return {
        conversationId: conversation,
        text: lastMsg.text,
        user: lastMsg.user,
        agent: result.parentData.agent,
        ended,
        userNewmessage: userNewmessage,
        createdAt: lastMsg.createdAt,
      };
    },

    async createNewConvo() {
      console.log("click");
      this.isLoading = true;
      const savedState = JSON.parse(localStorage.getItem("appState"));

      const conversationsById = savedState.conversations.byId;

      const lastIdKey = Object.keys(conversationsById).pop();

      const lastConversation = Object.values(conversationsById).pop();
      const lastConversationEnded = lastConversation
        ? lastConversation.ended
        : true;

      if (lastConversationEnded) {
        this.isLoading = true;
        let chatId = await createConversationId(this.user);
        // console.log(chatId);
        await this.saveConversationId(chatId);
        this.isLoading = false;
        this.show(chatId);
      } else {
        this.show(lastIdKey);
        this.isLoading = false;
      }
    },

    isConversationIdExpired() {
      const conversationId = localStorage.getItem("conversationId");
      if (conversationId) {
        const conversationTimestamp = parseInt(conversationId.split("-")[1]);
        const now = Date.now();
        const twoDaysInMilliseconds = 7 * 24 * 60 * 60 * 1000; // 2 days in milliseconds
        return now - conversationTimestamp > twoDaysInMilliseconds;
      }
      return true; // conversation ID not found or invalid
    },

    async init() {
      const savedState = JSON.parse(localStorage.getItem("appState"));

      const conversationsById = savedState.conversations.byId;

      const valuesArray = [];

      for (const id in conversationsById) {
        const obj = { ...conversationsById[id], id };
        valuesArray.push(obj);
      }

      const reversedArray = valuesArray.reverse(); // reverse the order of the array

      const firstThree = reversedArray.slice(0, 3); // get the first three elements of the reversed array

      //   console.log(firstThree, "data i want");

      for (let i = 0; i < firstThree.length; i++) {
        const element = firstThree[i];
        let convoInfo = await this.getAllInfo(element.id);

        // Check if the conversation already exists in the array
        const index = this.conversations.findIndex(
          (c) => c.conversationId === convoInfo.conversationId
        );

        // If the conversation exists, update the existing object
        if (index !== -1) {
          this.conversations[index] = convoInfo;
        } else {
          // Otherwise, add the new object to the array
          this.conversations.push(convoInfo);
        }
      }

      console.log(this.conversations, "this is fine");

      if (firstThree.length > 0) {
        this.socketMainpage.emit("join", {
          id: firstThree[0].id,
          user: this.user,
        });
      }

      //   console.log(this.conversations, "data i want");

      this.isLoading = false;
    },
  },

  async created() {
    this.isLoading = true;
    this.socketMainpage = io("http://18.212.173.184:3000/");
    this.socketMainpage.on("connect", () => {
      //   console.log("Connected to server");
    });

    this.socketMainpage.on("newMessage", (message) => {
      message.createdAt = Timestamp.now();
      //   console.log(message.conversationId, this.user, "the new message");
      this.handleNewMessage(message);
    });

    const appState = JSON.parse(localStorage.getItem("appState"));
    if (appState) {
      
      if (appState.state.page.name === "conversation") {
        this.conversationId = appState.state.page.id;
        this.showingConvo = true;
      }
    }

    await this.init();
  },
  computed: {
    sortedConversations() {
      return this.conversations.slice().sort((a, b) => {
        return b.createdAt.seconds - a.createdAt.seconds;
      });
    },

    activeConversations() {
      return this.conversations.filter((c) => !c.ended);
    },
    endedConversations() {
      return this.conversations.filter((c) => c.ended);
    },

    getCount() {
      const endedCount = this.conversations.reduce((count, message) => {
        return count + (!message.ended ? 1 : 0);
      }, 0);
      //   console.log("computed", endedCount);

      return endedCount > 0;
    },
  },
};
</script>

<style scoped>
.chat-container {
  width: 100%;

  background-color: #fff;
  border: 1px solid rgb(242, 242, 242);
  box-shadow: rgba(0, 0, 0, 0.1) 6px 2px 15px 5px;
  /* border: 1px solid #ccc; */

  overflow: hidden;
  font-family: Arial, sans-serif;
  display: none; /* Hide the widget by default */
  z-index: 9998;
  display: flex;
  flex-direction: column;
}

/* Media query for larger screens */
@media (min-width: 600px) {
  .chat-container {
    position: absolute;
    bottom: 90px;
    height: 90%;
    right: 20px;
    max-width: 400px; /* Adjust the maximum width as per your requirement */
    max-height: 80%; /* Adjust the maximum height as per your requirement */
    border-radius: 5px;
  }
  .chat-header {
    border-radius: 10px 10px 0 0;
  }
}

/* Media query for mobile view */
@media (max-width: 600px) {
  .chat-container {
    position: fixed;
    bottom: 80px;
    height: 89%;
    max-width: none;
    max-height: none;
    border-radius: 0 0 10px 10px;
  }

  .chat-header {
    border-radius: 0;
  }
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f0f0f0;
  padding: 10px;
  text-align: center;
  background: #0056ff 0% 0% no-repeat padding-box;
  color: #ffffff;
}

.chat-header h3 {
  margin: 0;
}

.chat-header .fa-times {
  cursor: pointer;
}

.chat-body {
  flex-grow: 1;
  overflow-y: auto;
  padding: 15px;
  overflow-x: hidden;
}

.chat-body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

.chat-body::-webkit-scrollbar {
  width: 12px;
  background-color: #f5f5f5;
}

.chat-body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}

.chat-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.chat-footer input[type="text"] {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 20px;
  margin-right: 20px;
}

.chat-footer textarea {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 20px;
  margin-right: 6px;
  resize: none;
  overflow: auto;
  max-height: 200px;
}

.chat-footer > .footButton {
  padding: 10px 20px;
  color: #ffffff;
  background-color: rgb(17, 103, 255);
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.chat-footer > .footIcon {
  margin-left: 5px;
  margin-right: 5px;
  cursor: pointer;
}

.chat-footer .start {
  width: 100%;
  padding: 10px 20px;
  color: #ffffff;
  background-color: rgb(17, 103, 255);
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.chat-footer .text {
  text-align: center;
  font-size: 14px;
  line-height: 21px;
  padding-top: 0px;
  color: rgb(115, 115, 115);
}
</style>
