<template>
  <div id="chatview">
    <transition name="bounce">
      <div class="chat-container">
        <div class="chat-header">
          <v-icon
            style="cursor: pointer"
            @click="$emit('goBack')"
            start
            icon="mdi-arrow-left"
          ></v-icon>
          <h3>Chatly</h3>
        </div>

        <div
          ref="chatbody"
          class="chat-body"
          id="chat-body"
          style="display: flex; justify-content: center; align-items: center"
        >
          <div v-if="isLoading" class="loading-div"></div>

          <div style="max-width: 360px" class="">
            <v-form ref="form">
              <h3 style="text-align: center">
                Please tell us how was your experience?
              </h3>
              <v-btn-toggle v-model="selectedEmoji" mandatory>
                <v-btn
                  v-for="emoji in emojis"
                  :key="emoji.value"
                  :value="emoji.value"
                  class="ma-2 custom-btn"
                >
                  <span class="emoji">{{ emoji.label }}</span>
                  <v-tooltip top>
                    <span class="tooltip-value">{{ emoji.value }}</span>
                  </v-tooltip>
                </v-btn>
              </v-btn-toggle>
              <v-text-field
                v-model="comments"
                label="Additional Comments"
                :rules="rules.comments"
              ></v-text-field>
              <v-btn
                color="blue"
                @click="submitFeedback"
                :disabled="selectedEmoji === null"
                >Submit</v-btn
              >
            </v-form>
          </div>
        </div>

        <div class="chat-footer">
          <!-- <div
                style="
                  margin: 24px auto 12px;
                  text-align: center;
                  font-size: 14px;
                  line-height: 21px;
                  padding-top: 0px;
                  color: rgb(115, 115, 115);
                "
              >
                Your conversation has ended
              </div>
              <button @click="createNewConvo" class="start">
                Start Conversation
              </button> -->
        </div>
      </div>
    </transition>
    <ErrorDialog
      v-if="showErrorDialog"
      :error="errorMessage"
      @dismiss="hideErrorDialog"
    />
  </div>
</template>

<script>
import { updateConvo } from "@/utils/firbase_utils";
export default {
  data() {
    return {
      selectedEmoji: null,
      comments: "",
      isLoading: false,
      emojis: [
        { value: "bad", label: "😠" },
        { value: "fair", label: "😕" },
        { value: "good", label: "😊" },
        { value: "great", label: "😄" },
      ],
      rules: {
        comments: [
          (value) => !!value || "Comments are required",
          (value) =>
            value.length <= 1000 ||
            "Comments must be less than 1000 characters",
        ],
      },
    };
  },

  components: {},
  props: ["conversationId"],

  methods: {
    async updateConversation(conversationId, conversationData) {
      const data = JSON.parse(localStorage.getItem("appState"));
      const newState = {
        ...data,
        conversations: {
          ...data.conversations,
          byId: {
            ...data.conversations.byId,
            [conversationId]: {
              ...data.conversations.byId[conversationId],
              ...conversationData,
            },
          },
        },
      };
      //   this.updateState(newState);
      console.log(newState, "updateing feedback");
      localStorage.setItem("appState", JSON.stringify(newState));
    },

    getConversationBy(conversationId) {
      const data = JSON.parse(localStorage.getItem("appState"));
      const conversations = data.conversations.byId;
      if (conversations.hasOwnProperty(conversationId)) {
        return conversations[conversationId];
      } else {
        return null;
      }
    },

    async submitFeedback() {
      if (this.$refs.form.validate()) {
        await updateConvo(this.conversationId, {
          ended: true,
          comment: this.comments,
          reaction: this.selectedEmoji,
        });
        let item = this.getConversationBy(this.conversationId);
        console.log("the item", item);
        item.ended = true;
        // console.log(item, "the item");
        await this.updateConversation(this.conversationId, item);
        this.$emit("goBackTo");
        // this.$router.push({ name: "home" });
      }
      // Code to submit feedback goes here
    },
  },

  created() {},
};
</script>
<style scoped>
