  display: -ms-flexbox;
  justify-content: center;
  -webkit-justify-content: center;
  align-items: center;
  box-sizing: border-box;
  background: #f4f3f6 0% 0% no-repeat padding-box;
  display: inline-block;
  padding: 20px 15px;
  /* padding-right: 50px; */
