}

.line-loader {
  height: 4px;
  background-color: #ccc;
  position: relative;
}

.line-loader:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #007aff;
  animation: line-loader 5s linear;
  animation-fill-mode: forwards;
}

@keyframes line-loader {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

.loading-div {
  position: absolute;
  /* top: 0; */
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-div::after {
  content: "";
  width: 40px;
  height: 40px;
  border: 3px solid #ccc;
  border-top-color: #333;
  border-radius: 50%;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.recordingIcon {
